.btnrecord {
  position: absolute;
  left: 50%;
  top: 85%;
  width: 100px;
  height: 100px;
  margin: -50px -50px; }
  .btnrecord #btn {
    display: none; }
    .btnrecord #btn + label:before {
      position: absolute;
      left: 50%;
      top: 85%;
      width: 80px;
      height: 80px;
      margin: -40px -40px;
      content: '';
      -webkit-transform: translate(-6px, -6px);
      transform: translate(-6px, -6px);
      border-radius: 50%;
      border: 6px solid #fff;
      cursor: pointer; }
    .btnrecord #btn + label:after {
      position: absolute;
      left: 50%;
      top: 85%;
      width: 70px;
      height: 70px;
      margin: -35px -35px;
      content: '';
      border-radius: 35px;
      background: #e80415;
      cursor: pointer; }
    .btnrecord #btn:checked + label:after {
      -webkit-animation: stop 0.5s infinite cubic-bezier(0.4, -0.9, 0.9, 1);
      animation: stop 0.5s infinite cubic-bezier(0.4, -0.9, 0.9, 1);
      -webkit-animation-iteration-count: 1;
      animation-iteration-count: 1;
      -webkit-animation-fill-mode: forwards;
      animation-fill-mode: forwards; }
    .btnrecord #btn:checked ~ .time {
      -webkit-animation: time 0.3s infinite linear;
      animation: time 0.3s infinite linear;
      -webkit-animation-iteration-count: 1;
      animation-iteration-count: 1;
      -webkit-animation-fill-mode: forwards;
      animation-fill-mode: forwards;
      -webkit-animation-delay: 0.3s;
              animation-delay: 0.3s; }
    .btnrecord #btn:checked ~ .time .h_m:after {
      -webkit-animation: to_24 86400s infinite linear;
      animation: to_24 86400s infinite linear;
      -webkit-animation-delay: 1800s;
      animation-delay: 1800s; }
    .btnrecord #btn:checked ~ .time .h_m:before {
      -webkit-animation: to_60 3600s infinite linear;
      animation: to_60 3600s infinite linear;
      -webkit-animation-delay: 30s;
      animation-delay: 30s; }
    .btnrecord #btn:checked ~ .time .s_ms:before {
      -webkit-animation: to_60 60s infinite linear;
      animation: to_60 60s infinite linear;
      -webkit-animation-delay: 0.5s;
      animation-delay: 0.5s; }
    .btnrecord #btn:checked ~ .time .s_ms:after {
      -webkit-animation: to_100 1s infinite linear;
      animation: to_100 1s infinite linear; }
  .btnrecord .time {
    position: relative;
    width: 130%;
    opacity: 0; }
    .btnrecord .time > * {
      display: inline-block;
      width: 50%;
      margin: -2px;
      color: #a2a2a2;
      font-family: 'Fjalla One', sans-serif;
      font-size: 1.3em; }
    .btnrecord .time .hh.h_m:after {
      float: right;
      position: relative;
      content: "00:"; }
    .btnrecord .time .h_m:before {
      float: right;
      position: relative;
      content: "00:"; }
    .btnrecord .time .s_ms:before {
      float: left;
      position: relative;
      content: "00:"; }
    .btnrecord .time .ms.s_ms:after {
      float: left;
      position: relative;
      content: "00"; }

.toggleswitch {
  position: absolute;
  right: 0px;
  bottom: 40px;
  padding: 8px;
  min-width: 140px; }

.switch {
  position: absolute !important;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  bottom: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }

.switch-label {
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: inline-block;
  position: relative;
  height: 44px;
  width: 90px;
  background: #cece;
  line-height: 44px;
  vertical-align: middle;
  transition: background-color 500ms ease;
  cursor: pointer;
  font-size: 0;
  color: transparent;
  border-radius: 22px; }
  .switch-label:after {
    background-color: #e8e8e8;
    border-radius: 50%;
    box-shadow: -2px 0px 0px rgba(0, 0, 0, 0.1);
    content: '';
    display: block;
    height: 40px;
    position: absolute;
    right: 48px;
    top: 2px;
    transition: right 500ms ease, background-color 500ms ease, box-shadow 500ms ease;
    width: 40px; }

.switch:checked + label {
  background: #00ff2b; }

.switch:checked + label:after {
  right: 2px;
  box-shadow: 2px 0px 0px rgba(0, 0, 0, 0.15);
  background-color: #fff; }

