}

.video-overlay {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}

.fps-counter {
  position: absolute;
  bottom: 10px;
  left: 10px;
  color: #ffff00;
  font-size: 11px;
  font-family: 'Courier New', Courier, monospace;
  background: #000;
}
