@import "../sass/mixins";

$cont-size: 50px;

.toggleswitch {
  position: absolute;
  right: 0px;
  bottom: 40px;
  padding: 8px;
  min-width: 140px;
}

.switch {
  position: absolute !important;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  bottom: 0;
  user-select: none;
}

.switch-label {
  cursor: pointer;
  user-select: none;
  display: inline-block;
  position: relative;
  height: 44px;
  width: 90px;
  background: #cece;
  line-height: 44px;
  vertical-align: middle;
  transition: background-color 500ms ease;
  cursor: pointer;
  font-size: 0;
  color: transparent;
  border-radius: 22px;

  &:after {
    background-color: #e8e8e8;
    border-radius: 50%;
    box-shadow: -2px 0px 0px rgba(0,0,0,0.1);
    content: '';
    display: block;
    height: 40px;
    position: absolute;
    right: 48px;
    top: 2px;
    transition: right 500ms ease, background-color 500ms ease, box-shadow 500ms ease;
    width: 40px;
  }
}
.switch:checked + label {
  background: #00ff2b;
}
.switch:checked + label:after {
  right: 2px;
  box-shadow: 2px 0px 0px rgba(0, 0, 0, 0.15);
  background-color: #fff;
}
